/**
*------------------------------------------------
*   = GLOBAL STYLE
*------------------------------------------------
*/
:root {
    --ff: verdana;
    --width: 85vw;
    --padding-top: 5rem;
    --paddin-bottom: 5rem;
}

html {
    font-size: 100%;
}

body {
    font: 1em/1.25em "Cabin", Helvetica, Arial, sans-serif;
    text-align: center;
    color: var( --text-color );

    .big-wrapper {
        background: var( --transparent );
        display: flex;
        flex-direction: column;
        min-height: 100vh;

        & > section{
            background: var( --transparent );
            margin-top: 5.3rem;
        }

        .azad-container {
            width: var( --width );
            margin: 0 auto;
        }

        .azad-stretched {
            //width: 100%;
        }
        
        .azad-row {
            display: flex;
            overflow: overlay;
        }
    }   
}

ol,
ul {
    list-style: none;
}

a { 
    text-decoration: none;
}

.screen-reader-text {
    border: 0;
    clip: rect( 1px, 1px, 1px, 1px );
    -webkit-clip-path: inset( 50% );
    clip-path: inset( 50% );
    height: 1px;
    margin: -1px;
    overflow: hidden;
    padding: 0;
    position: absolute !important;
    width: 1px;
    color: red;
    word-wrap: normal !important;
    word-break: normal;
}